import React from 'react'
import { useEffect} from 'react'
import {Link} from "react-router-dom"
import { Image,Container,Row,Col } from 'react-bootstrap'
import { Slide} from "react-awesome-reveal";
import shorts1 from "../images/shorts/shorts1.jpeg"
import shorts2 from "../images/shorts/shorts2.jpeg"
import shorts3 from "../images/shorts/shorts3.jpeg"
import shorts4 from "../images/shorts/shorts4.jpeg"
import shorts5 from "../images/shorts/shorts5.jpeg"


function Shorts() {
    useEffect(()=>{
        window.scrollTo(0, 0)
    })
    return (
        <div>
            
            <h1 className="pageHeading"><Link to="/"><i class="fa fa-home" style={{"font-size":"36px","margin":"10px","color":"skyblue"}}></i></Link>Shorts</h1>
            <Container>
            <Row>
                <Col xs={12} sm={12} md={4}>
                <Slide  triggerOnce duration="800">
                <Image className="shorts" src={shorts1} rounded style={{"margin":"10px"}}/>
                </Slide>
                </Col>
                
                <Col xs={12} sm={12} md={4}>
                <Slide  triggerOnce duration="1000">
                <Image className="shorts" src={shorts2} rounded style={{"margin":"10px"}}/>
                </Slide>
                </Col>
                <Col xs={12} sm={12} md={4}>
                <Slide  triggerOnce duration="1200">
                <Image className="shorts" src={shorts3} rounded style={{"margin":"10px"}}/>
                </Slide>
                </Col>
                <Col xs={12} sm={12} md={4}>
                <Slide  triggerOnce duration="800">
                <Image className="shorts" src={shorts4} rounded style={{"margin":"10px"}}/>
                </Slide>
                </Col>
                <Col xs={12} sm={12} md={4}>
                <Slide  triggerOnce duration="1000">
                <Image className="shorts" src={shorts5} rounded style={{"margin":"10px"}}/>
                </Slide>
                </Col>
                
            </Row>
            </Container>
        </div>
    )
}

export default Shorts
